.TodoItem {
    background-color: #fafafa;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 24px;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.15);
    border-radius: 12px;
    transition: all .5s ease-in-out;
}

.TodoItem:hover {
    transform: scale(1.05);
}

.TodoItem-p {
    margin: 24px 0 24px 24px;
    width: calc(100% - 120px);
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
}
.TodoItem-p--complete {
    text-decoration: line-through;
}

.Icon {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 48px;
    width: 48px;
    font-size: 24px;
    font-weight: bold;
  /* background-color: #CCC; */
}

.Icon-check {
    position: absolute;
    left: 12px;
    font-size: 35px;
}

.Icon-check:hover {
    color:#61DAFA;
}


.Icon-check--active {
    color: #4caf50;
}

.Icon-delete {
    position: absolute;
    right: 12px;
    font-size: 35px;
}
.Icon-delete:hover {
    color: red;
}
